.Button-module__button__Mxh5t {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  color: var(--button-color);
  background-color: var(--button-background);
  cursor: pointer;
}

.Button-module__button__Mxh5t:focus,
.Button-module__button__Mxh5t:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

.Button-module__button__Mxh5t:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.Button-module__pagination-button__KcpSC {
  background-color: var(--pagination-button-bg);
}

.Button-module__pagination-button__KcpSC:hover {
  background-color: var(--pagination-button-hover-bg);
}

.Button-module__search-button__OYIlU {
  background-color: var(--search-button-bg);
}

.Button-module__search-button__OYIlU:hover {
  background-color: var(--search-button-hover-bg);
}

.Button-module__error-boundary-button__VuxHr {
  color: var(--error-boundary-button-color);
}

.Button-module__toggle-theme-button__HuaIf {
  background-color: var(--toggle-theme-button-bg);
}

.Button-module__toggle-theme-button__HuaIf:hover,
.Button-module__error-boundary-button__VuxHr:hover {
  background-color: var(--button-hover-background);
}
