.Card-module__result-item__kzOWQ {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-radius: 0.5rem;
  width: 400px;
  background-color: var(--result-item-background);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.Card-module__result-item__kzOWQ h3 {
  margin: 0 0 10px;
  text-align: center;
}

.Card-module__attributes-grid__bQmZO {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.Card-module__attributes-grid__bQmZO p {
  background-color: var(--result-attributes-grid-background);
  padding: 5px;
  border-radius: 4px;
  margin: 0;
}

